import React from "react";
import styled from "styled-components";

import FlexBox from "../FlexBox/index";

const StyledHeaderLayout = styled(FlexBox)`
  width: 100px;
  height: 100px;
  background-color: #555;
`;

export default function HeaderLayout(): JSX.Element {
  return (
    <>
      <FlexBox />
      <StyledHeaderLayout />
    </>
  );
}
